.cart-item {
  width: 100%;
  display: flex;
  height: 80px;
  margin-bottom: 15px;

  img {
    width: 35%;
  }

  .item-details {
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    padding: 10px 25px;
    font-size: 14px;

    .name {
      font-weight: bold;
      margin-bottom: 5px;
    }
  }
}
